<template>
  <div class="my-order">
    <van-nav-bar
      title="我的订单"
      left-arrow
      @click-left="goToMemberCenter"
    />

<!--    <div>{{countSalesTotalPrice()}}</div>-->
<!--    <div>{{countCashTotalPrice()}}</div>-->
<!--    <div>{{countBalanceTotalPrice()}}</div>-->

    <div class="div-content">
      <div class="div-card-box" v-for="item in dataList" :key="item.id">
        <div class="div-card-box-title-line">
          <span class="span-line-value">{{ item.date }}</span>
          <span class="span-line-value">{{item.payType}}支付</span>
        </div>

        <div class="div-sales-item">
          <div class="div-product-box-line" v-for="(product, index) in item.sales" :key="index">
            <span class="span-line-label">{{ product.productName }} ￥{{ product.salesAmount }}</span>
            <span class="span-line-value">×{{ product.quantity }}</span>
          </div>
        </div>

        <div class="div-total-box-line">
          <span class="span-line-label">合计</span>
<!--          <span class="span-line-pay">支付方式：-->
<!--            <span class="span-line-pay">{{item.payType}}</span>-->
<!--          </span>-->

          <span class="span-line-total">￥{{ item.totalPrice }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 引入 json 数据
import rawData from "@/utils/data.json";
export default {
  name: 'MyOrder',
  data() {
    return {
      dataList: [],
    }
  },
  created() {
    log('created rawData is ', rawData)
    // 倒序排序
    // this.dataList = this.dataList.reverse()
    this.init()
  },
  methods: {
    init() {
      const phone = this.$route.query.phoneNumber
      if (phone) {
        const orderData = rawData.orderData || {}
        const dataList = orderData[phone] || []
        this.dataList = dataList.reverse()
      } else {
        alert('请先登录')
        this.$router.push({
          name: 'LoginPage',
        })
      }
    },
    goToMemberCenter() {
      this.$router.push({
        name: 'MemberCenter',
        query: {...this.$route.query},
      })
    },
    // 计算销售总额
    countSalesTotalPrice() {
      const list = this.dataList
      let total = 0
      for (let i = 0; i < list.length; i++) {
        const item = list[i]
        total += item.totalPrice
      }

      return total
    },
    // 计算现金购买金额
    countCashTotalPrice() {
      const list = this.dataList
      let total = 0
      for (let i = 0; i < list.length; i++) {
        const item = list[i]
        if (item.payType === '现金') {
          total += item.totalPrice
        }
      }

      return total
    },
    // 计算余额购买金额
    countBalanceTotalPrice() {
      const list = this.dataList
      let total = 0
      for (let i = 0; i < list.length; i++) {
        const item = list[i]
        if (item.payType === '余额') {
          total += item.totalPrice
        }
      }

      return total
    },
  },
}

</script>

<style scoped>
.my-order >>> .van-nav-bar {
  background-color: #977961;
}

.my-order >>> .van-nav-bar__title {
  color: #fff;
}

.my-order >>> .van-icon-arrow-left {
  color: #fff;
}

.div-content {
  padding: 10px;
  padding-top: 0;
}

.div-card-box {
  margin-top: 10px;
  border-radius: 8px;
  padding: 12px 24px;
  background-color: #fff;
}

.div-card-box-title-line {
  display: flex;
  justify-content: space-between;
  padding-bottom: 4px;
  border-bottom: 1px solid #ccc;
  font-size: 14px;
}
.div-card-box-line {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ccc;
  font-size: 16px;
}

.div-card-box-line:last-child {
  border-bottom: none;
}

.span-line-label {
  color: #333;
}

.span-line-value {
  color: #999;
  font-weight: 400;
}

.div-sales-item {
  padding: 4px 0;
}

.div-product-box-line {
  display: flex;
  justify-content: space-between;
  padding: 2px 0;
  font-size: 14px;
  font-weight: 400;
}

.div-total-box-line {
  display: flex;
  justify-content: space-between;
  line-height: 18px;
  padding-top: 10px;
  font-size: 16px;
  font-weight: 400;
}

.span-line-total {
  line-height: 18px;
  font-size: 18px;
  font-weight: 600;
}
</style>



